  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  background: #FFF;
  overflow-y: auto;
  /*opacity: 0;

  transition-timing-function: linear;
  transition: all .2s;
  -moz-transition: all .2s;
  -webkit-transition: all .2s;
  -o-transition: all .2s;*/
}
/*.popup-images.show {
  opacity: 1;
}*/
.popup-images .main {
  max-width: 720px;
  margin: 0 auto;
  text-align: center;
  padding-top: 1.5rem;
}
.popup-images .btn-close {
  font-size: 3rem;
  margin: 0 auto;
  opacity: 0.2;
}
.popup-images .split {
  margin-top: 1rem;
}

.slider-item .photo-item {
  width: 100%;
}

/*.slider-item img {
  width: 100%;
}*/
.slider-item h2 {
  font-size: 0.875rem;
  color: #4A4A4A;
  padding: 1rem 0 0.5rem 0;
}
.slider-item h2 a:link,
.slider-item h2 a:visited {
  color: #4A4A4A;
}
.slider-item .tag-list a:first-child::before {
  content: '# ';
}
.slider-item .tag-list a {
  font-size: 0.875rem;
  margin-right: 0.5rem;
}
.slider-item .tag-list a:link,
.slider-item .tag-list a:visited {
  color: #9B9B9B;
}



