











.nav {
  padding-top: 3.8rem;
  text-align: center;
}
.nav ul {
  width: 80%;
  margin: 0 auto;
}
.nav li {
  width: 100%;
  height: 1.6rem;
  line-height: 1.8rem;
  margin-bottom: 5px;
  font-size: 0.875rem;
  text-align: center;
}
.nav li a span {
  color: #4A4A4A;
  background: #FFF;
  width: 30%;
  padding: 0 10px;
  display: block;
  margin: 0 auto;
}
.nav .on {
  display: block;
  background-image: url('../../assets/img/line-dark.png');
  background-repeat: repeat-x;
  background-position: center;
}
.nav li a.on span {
  color: #000;
}



