

















































.image-item {
  display: block;
  padding-bottom: 1rem;
}
.image-item h2 {
  font-size: 0.875rem;
  color: #4A4A4A;
  padding: 1rem 0 0.5rem 0;
}
.image-item h2 a:link,
.image-item h2 a:visited {
  color: #4A4A4A;
}
.image-item img {
  width: 100%;
}
.image-item .tag-list a:first-child::before {
  content: '# ';
}
.image-item .tag-list a {
  font-size: 0.875rem;
  margin-right: 0.5rem;
}
.image-item .tag-list a:link,
.image-item .tag-list a:visited {
  color: #9B9B9B;
}



